<template lang='pug'>
  div
    .card
      router-link.card-text(:to='address') {{title}}
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true
    },
    address: {
      type: String,
      default: '#'
    }
  }
}
</script>

<style scoped>
.card {
  border-radius: 2px;
  margin-bottom: 15px;
  background-color: #fff;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
}
.card-text {
  display: block;
  font-size: 16px;
  color: #333;
  line-height: 56px;
  padding-left: 20px;
}
</style>



